




































































import Vue from 'vue';

export default Vue.extend({
created() {
    
    },
    props: ['id'],
    data() {
        return {
            router: this.$router
        }
    },

    methods: {
        routeToDetail(param: string): void {
            this.router.push({
                path: `/words/wordsDetail/${param}`
            });
        }
    }
})
    

